import "./App.css";
import React, { useState, useEffect } from "react";
import ReactGA from 'react-ga';
import Navbar from "./components/Navbar";
import Page from "./components/Page";
import Footer from "./components/Footer";

// Initialize Google Analytics
ReactGA.initialize('G-H99G63HB34');

function App() {
  
  const [pages] = useState([
    {
      name: "Home",
    },
    {
      name: "Let's Talk",
    },
  ]);

  // sets the current page to 0 and can be dynamically updated based on the buttons that are clicked inside the components
  const [currentPage, setCurrentPage] = useState(pages[0]);

  // Use an effect to detect when the currentPage changes
  useEffect(() => {
    // Log the page view with Google Analytics
    ReactGA.pageview(currentPage.name);
  }, [currentPage]); // Only re-run the effect if currentPage changes

  // Track initial pageview when the component mounts
  useEffect(() => {
    ReactGA.pageview(pages[0].name); // Log the first page in your pages array
  }, []); // Empty dependency array, so this effect runs only once

  return (
    <>
      <Navbar
        pages={pages}
        setCurrentPage={setCurrentPage}
        currentPage={currentPage}
      />
      <div id="top"></div>
      <main>
        <Page
          pages={pages}
          setCurrentPage={setCurrentPage}
          currentPage={currentPage}
        ></Page>
      </main>
      <Footer pages={pages} setCurrentPage={setCurrentPage} />
    </>
  );
}

export default App
